import React from 'react'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const Logo = () => {
    const data = useStaticQuery(
      graphql`
        query {
          file(relativePath: { eq: "magemastery.png" }) {
            childImageSharp {
              fixed(width: 250) {
                 ...GatsbyImageSharpFixed
              }
            }
          }
        }
      `
    )

    return <Img fixed={data.file.childImageSharp.fixed} width="250" alt="Mage Mastery"/>
}

export default Logo
